import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import { BtnSave, BtnCancel } from 'components/common/button';
import { SelectGender, SelectAjax } from 'components/common/index';
import { Row, Col, Card, Form, Input, InputNumber, DatePicker } from 'antd';
import { getCandidateById, updateCandidate, createCandidate } from 'services/studentCandidate';
import { required } from 'utils/validateRules';
import { getAreas } from '../../services/area';
import { getListTown } from '../../services/town';
import { getEnrollSolutions } from '../../services/enrollSolution';

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    code: '',
    esId: '',
    firstname: '',
    lastname: '',
    dob: '',
    pobId: '',
    gender: '',
    schoolId: '',
    areaId: '',
    graduatedYear: '',
    studyField: '',
    testGroup: '',
    gradeM1: '',
    gradeM2: '',
    gradeM3: '',
    certType: '',
    trainTypeId: '',
  };
  return { ...initialValueDefault, ...initialValue };
};

@Form.create({ name: 'CandidateStudentFormPage' })
class CandidateStudentFormPage extends Component {
  state = {
    dataId: false,
    initialValue: {},
  };

  componentDidMount = () => {
    const { match } = this.props;
    if (match.params.id) {
      this.setState({ dataId: match.params.id });
      this.getDataById(match.params.id);
    }
  };

  getDataById = async id => {
    const res = await getCandidateById(id);
    const initialValue = handleInitialValue(res.data);
    this.setState({ initialValue });
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      this.saveData(values);
    });
  };

  saveData = async values => {
    const { dataId } = this.state;
    try {
      if (!dataId) {
        await createCandidate(values);
      } else {
        await updateCandidate({ id: dataId, ...values });
      }
      this.props.history.goBack();
    } catch (error) {
      console.log(error);
    }
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    return (
      <Card title="Thm ng vin">
        <Form {...formItemLayout} onSubmit={this.handleSubmit}>
          <Row>
            <Col span={12}>
              <Form.Item label="H" hasFeedback>
                {getFieldDecorator('firstname', {
                  initialValue: initialValue.firstname,
                  rules: [required],
                })(<Input />)}
              </Form.Item>
              <Form.Item label="Tn" hasFeedback>
                {getFieldDecorator('lastname', {
                  initialValue: initialValue.lastname,
                  rules: [required],
                })(<Input />)}
              </Form.Item>
              <Form.Item label="SBD" hasFeedback>
                {getFieldDecorator('code', {
                  initialValue: initialValue.code,
                  rules: [required],
                })(<Input />)}
              </Form.Item>
              <Form.Item label="Ni sinh" hasFeedback>
                {getFieldDecorator('pobId', {
                  initialValue: initialValue.pobId,
                  rules: [required],
                })(<SelectAjax service={getListTown} />)}
              </Form.Item>
              <Form.Item label="Ngy sinh" hasFeedback>
                {getFieldDecorator('dob', {
                  initialValue: initialValue.dob,
                  rules: [required],
                })(<DatePicker />)}
              </Form.Item>
              <Form.Item label="Gii tnh" hasFeedback>
                {getFieldDecorator('gender', {
                  initialValue: initialValue.gender,
                  rules: [required],
                })(<SelectGender />)}
              </Form.Item>
              <Form.Item label="HK" hasFeedback>
                {getFieldDecorator('hk', {
                  initialValue: initialValue.hk,
                  rules: [required],
                })(<Input />)}
              </Form.Item>
              <Form.Item label="M trng" hasFeedback>
                {getFieldDecorator('schoolId', {
                  initialValue: initialValue.schoolId,
                  rules: [required],
                })(<Input />)}
              </Form.Item>
              <Form.Item label="Khu vc" hasFeedback>
                {getFieldDecorator('areaId', {
                  initialValue: initialValue.areaId,
                  rules: [required],
                })(<SelectAjax service={getAreas} />)}
              </Form.Item>
            </Col>
            <Col span={12}>
              <Form.Item label="Phng n TS" hasFeedback>
                {getFieldDecorator('esId', {
                  initialValue: initialValue.esId,
                  rules: [required],
                })(<SelectAjax service={getEnrollSolutions} />)}
              </Form.Item>
              <Form.Item label="Nm TN" hasFeedback>
                {getFieldDecorator('graduatedYear', {
                  initialValue: initialValue.graduatedYear,
                  rules: [required],
                })(<InputNumber min={1000} max={new Date().getFullYear()} />)}
              </Form.Item>
              <Form.Item label="H" hasFeedback>
                {getFieldDecorator('trainTypeId', {
                  initialValue: initialValue.trainTypeId,
                  rules: [required],
                })(<Input />)}
              </Form.Item>
              <Form.Item label="Ngnh" hasFeedback>
                {getFieldDecorator('studyField', {
                  initialValue: initialValue.studyField,
                  rules: [required],
                })(<Input />)}
              </Form.Item>
              <Form.Item label="Khi" hasFeedback>
                {getFieldDecorator('testGroup', {
                  initialValue: initialValue.testGroup,
                  rules: [required],
                })(<Input />)}
              </Form.Item>
              <Form.Item label="Mn 1" hasFeedback>
                {getFieldDecorator('gradeM1', {
                  initialValue: initialValue.gradeM1,
                  rules: [required],
                })(<InputNumber min={0} max={10} />)}
              </Form.Item>
              <Form.Item label="Mn 2" hasFeedback>
                {getFieldDecorator('gradeM2', {
                  initialValue: initialValue.gradeM2,
                  rules: [required],
                })(<InputNumber min={0} max={10} />)}
              </Form.Item>
              <Form.Item label="Mn 3" hasFeedback>
                {getFieldDecorator('gradeM3', {
                  initialValue: initialValue.gradeM3,
                  rules: [required],
                })(<InputNumber min={0} max={10} />)}
              </Form.Item>
            </Col>
          </Row>
          <div style={{ textAlign: 'center' }}>
            <BtnSave htmlType="submit" />
            <BtnCancel onClick={() => this.props.history.goBack()} />
          </div>
        </Form>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <CandidateStudentFormPage {...props} />
  </Page>
));



